import { Layout, Menu, Breadcrumb, Button, PageHeader, Popconfirm } from "antd";
import {
  DesktopOutlined,
  NotificationOutlined,
  FileOutlined,
  TeamOutlined,
  UserOutlined,
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  BookOutlined,
  QuestionCircleOutlined,
} from "@ant-design/icons";
import React from "react";
import "./Layout.css";
import { instanceOf } from "prop-types";
import { withCookies, Cookies } from "react-cookie";
import { Link } from "react-router-dom";
import Axios from "axios";

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

class CusLayout extends React.Component {
  static propTypes = {
    cookies: instanceOf(Cookies).isRequired,
  };
  constructor(props) {
    super(props);
    const { cookies } = props;
    this.state = {
      collapsed: true,
      token: cookies.get("tkn"),
      theme: localStorage.getItem("theme").toString() || "light",
    };
    console.log(this.state.theme, typeof this.state.theme);
  }

  onCollapse = (collapsed) => {
    // console.log(collapsed);
    this.setState({ collapsed });
  };

  customTrigger = () => {
    return React.createElement(
      this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
      {
        className: "anticon anticon-left",
        style: {
          color: this.state.theme == "light" ? "black" : "white",
          fontSize: "20px",
          marginTop: "15px",
        },
      }
    );
  };

  logout = async () => {
    const { cookies } = this.props;
    let i = await Axios.post("dj-rest-auth/logout/");
    let res = await i.status;
    if (res == 200) {
      cookies.remove("tkn");
      document.location.reload();
    }
  };

  render() {
    const { collapsed } = this.state;
    return (
      <Layout style={{ minHeight: "100vh", overflow: "hidden" }}>
        <Sider
          trigger={this.customTrigger()}
          collapsible
          collapsed={collapsed}
          onCollapse={this.onCollapse}
          style={{
            overflow: "auto",
            height: "100vh",
            position: "fixed",
            left: 0,
            zIndex: 1,
          }}
          theme={this.state.theme == "light" ? "light" : "dark"}
        >
          <div className="logo" />
          <Menu
            defaultSelectedKeys={["1"]}
            mode="inline"
            // style={{
            //   color:
            //     this.state.theme == "light" ? "rgba(0,0,0,0.87)" : "#ECECEC",
            //   backgroundColor:
            //     this.state.theme == "light" ? "rgba(0,0,0,0.87)" : "#242424",
            // }}
            theme={this.state.theme == "light" ? "light" : "dark"}
          >
            <Menu.Item key="1" icon={<NotificationOutlined />}>
              <Link to="/announcement">Announcement</Link>
            </Menu.Item>
            <Menu.Item key="2" icon={<BookOutlined />}>
              Course
            </Menu.Item>
            <SubMenu key="sub2" icon={<TeamOutlined />} title="Team">
              <Menu.Item key="6">Team 1</Menu.Item>
              <Menu.Item key="8">Team 2</Menu.Item>
            </SubMenu>
          </Menu>
        </Sider>
        <Layout
          className="site-layout"
          style={{
            marginLeft: collapsed ? 80 : 200,
            zIndex: 0,
            transition: ".2s",
          }}
        >
          <PageHeader
            className="site-layout-background"
            style={{
              padding: "10px",
              backgroundColor:
                this.state.theme == "light" ? "white" : "#002140",
              position: "fixed",
              width: "100%",
              left: "0",
              zIndex: "1",
            }}
            extra={[
              <Popconfirm
                placement="bottomRight"
                title="You about to logout, are you sure?"
                onConfirm={this.logout}
                okText="Yes"
                cancelText="No"
                icon={<QuestionCircleOutlined style={{ color: "red" }} />}
              >
                <Button key="1" type="primary">
                  Logout
                </Button>
              </Popconfirm>,
            ]}
          ></PageHeader>
          <Content style={{ margin: "60px 16px", zIndex: "0" }}>
            {this.props.children}
          </Content>
          <Footer style={{ textAlign: "center" }}>
            Ant Design Â©2018 Created by Ant UED
          </Footer>
        </Layout>
      </Layout>
    );
  }
}

export default withCookies(CusLayout);
