import React, { useEffect, useState } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import {
  LoginForm,
  PasswordResetForm,
  PasswordResetConfirm,
} from "./containers/Login";
import CusLayout from "./containers/Layout";
import { Announcement, AnnouncementDetail } from "./components/Announcement";
import { useCookies } from "react-cookie";
import axios from "axios";

const BaseRouter = (props) => {
  // console.log(props.verify);
  return props.verify ? (
    <CusLayout>
      <Switch>
        <Route exact path="/announcement" component={Announcement} />
        <Route exact path="/announcement/:id" component={AnnouncementDetail} />
        <Redirect from="*" to="/announcement" />
      </Switch>
    </CusLayout>
  ) : (
    <Switch>
      <Route exact path="/login" component={LoginForm} />
      <Route exact path="/password-reset" component={PasswordResetForm} />
      <Route
        path="/password-reset-confirm/:uid/:token"
        component={PasswordResetConfirm}
      />
      <Redirect from="*" to="/login" />
    </Switch>
  );
};

export default BaseRouter;
