import React, { useEffect, useState } from "react";
import { List, message, Avatar, Spin, Space } from "antd";
import { MessageOutlined } from "@ant-design/icons";
import InfiniteScroll from "react-infinite-scroller";
import axios from "axios";
import { convertUTC, fileType } from "./tools.js";
import FileViewer from "react-file-viewer";

export const Announcement = (props) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [hasMore, setHasmore] = useState(true);

  useEffect(() => {
    fetchData((res) => setData(res));
  }, []);

  const IconText = ({ icon, text }) => (
    <Space>
      {React.createElement(icon)}
      {text}
    </Space>
  );

  const fetchData = (callback) => {
    // console.log(axios.defaults.headers.common);
    axios.get("api/announcement/").then((res) => callback(res.data));
  };

  const handleInfiniteOnLoad = () => {
    setLoading(true);
    if (data.length > 10) {
      message.warning("Infinite List loaded all");
      setHasmore(false);
      setLoading(false);
      return;
    }
    fetchData((res) => {
      data = data.concat(res.results);
      setData(data);
      setLoading(false);
    });
  };
  return (
    <div className="demo-infinite-container">
      <InfiniteScroll
        initialLoad={false}
        pageStart={0}
        loadMore={handleInfiniteOnLoad}
        hasMore={!loading && hasMore}
        useWindow={false}
      >
        <List
          itemLayout="vertical"
          dataSource={data}
          renderItem={(item) => (
            <List.Item
              style={{
                border: "3px solid rgba(0,0,0,0.3)",
                borderRadius: "10px",
                padding: "20px",
                margin: "20px 5px",
                backgroundColor: "white",
              }}
              key={item.id}
              actions={[
                <IconText
                  icon={MessageOutlined}
                  text="2"
                  key="list-vertical-message"
                />,
              ]}
              onClick={
                () => console.log(props.history.push(`announcement/${item.id}`))
                // window.location.href = `announcement/${item.id}`)
              }
            >
              <List.Item.Meta
                title={<h4>{item.title}</h4>}
                description={item.author.username}
              />
              {convertUTC(item.date_posted)}
            </List.Item>
          )}
        >
          {loading && hasMore && (
            <div className="demo-loading-container">
              <Spin />
            </div>
          )}
        </List>
      </InfiniteScroll>
    </div>
  );
};

export const AnnouncementDetail = (props) => {
  const [item, setItem] = useState([]);
  const [error, setError] = useState(false);

  const fetchData = async (callback) => {
    try {
      let reponse = await axios
        .get(`api/announcement/${props.match.params.id}/`)
        .then((res) => callback(res));
      return Response.status;
    } catch (error) {
      setError(true);
    }
  };

  useEffect(() => {
    fetchData((res) => setItem(res.data));
  }, []);

  return item.author && !error ? (
    <List.Item
      style={{
        border: "3px solid rgba(0,0,0,0.3)",
        borderRadius: "10px",
        padding: "20px",
        margin: "20px 5px",
        backgroundColor: "white",
        minHeight: "300px",
      }}
      key={item.id}
    >
      <List.Item.Meta
        title={<h4>{item.title}</h4>}
        description={
          item.author.username + " | " + convertUTC(item.date_posted)
        }
      />
      {item.content}
      <div>
        {item.announcefile.map((f) => (
          <FileViewer fileType={fileType(f.file)} filePath={f.file} />
        ))}
      </div>
    </List.Item>
  ) : (
    <h3>404 Not found</h3>
  );
};
